.date__head { font-size: 7.10999rem; color: #fddb5e; font-family: 'Gilroy-Bold'; position: relative; margin-bottom: 4.266rem; }

.date__head--underline { border-bottom: 5px solid #fddb5e; width: 9%; height: 100%; position: absolute; z-index: 1; }

.month__event { position: relative; margin-bottom: 2.133rem; color: rgba(0, 0, 0, 0.9); font-size: 2.033rem; height: 2.1rem; overflow-y: hidden; }

.event__data { color: rgba(0, 0, 0, 0.9); font-size: 2.033rem; margin-bottom: 2.133rem; font-family: 'Gilroy-Light'; }
.event__data i { flex: 1; color: rgba(0, 0, 0, 0.9); font-size: 2.033rem; }

.c-read__more { position: absolute; bottom: 0; right: 0; cursor: pointer; }
.c-read__more i { color: #fddb5e; }

.height--auto { height: auto; }

.event__text, .event__time, .event__location { flex: 10; }

.front__paginate, .front__head, .front__content, .front__contact, .op-front__paginate, .op-front__head, .op-front__content, .op-front__know-more { width: 48.98789rem; }

.open-project { width: 100%; height: 100%; }

.op-front__left { flex: 1; width: 50%; }

.op-project__info { position: absolute; top: 11rem; margin-left: 2.85rem; width: 45%; }

.op-front__paginate { color: #fddb5e; font-family: 'Gilroy-Light'; }
.op-front__paginate h2 { letter-spacing: 1px; }
.op-front__paginate h2 a { text-decoration: none; color: #fddb5e; }

.op-front__head { margin-top: 2.84rem; }
.op-front__head h1 { line-height: 4.6rem; font-family: 'Montserrat', sans-serif; font-weight: 900; }
.op-front__head .op-front__sub-head { line-height: 2rem; color: #fddb5e; font-family: 'Gilroy-Light'; letter-spacing: 1px; margin-top: 1.4rem; }

.op-front__content { font-family: 'Gilroy-Light'; font-size: 1.4rem; color: #585858; letter-spacing: 1px; line-height: 2.844rem; margin-top: 2.5105rem; width: 53rem !important; }

.op-front__know-more { font-family: 'Montserrat', sans-serif; margin-top: 2.5rem; }
.op-front__know-more a { color: black; text-decoration: none; }
.op-front__know-more a h2 { font-weight: 400; }

.op-front__image { width: 56.4534rem; overflow: hidden; }
.op-front__image img { margin-left: -50%; height: 99.8vh; }


/*------------RANDOM-------------*/
.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto; }
